"use client"
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { supabase } from "../utils/supabaseClient";
import Image from "next/image";
const ProductDetail = () => {
  const { id } = useParams(); // Obtiene el ID del producto desde la URL
  const [product, setProduct] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchProduct = async () => {
      try {
        setLoading(true);

        // Consulta para obtener los datos del producto
        const { data: productData, error: productError } = await supabase
          .from("products")
          .select("*")
          .eq("id", id)
          .single();

        if (productError) {
          setError("Producto no encontrado.");
          return;
        }

        // Consulta para obtener las fragancias relacionadas
        const { data: fraganceData, error: fraganceError } = await supabase
          .from("fragances")
          .select("name, color, description")
          .in("id", productData.fragance);

        if (fraganceError) {
          setError("No se pudieron cargar las fragancias.");
          return;
        }

        // Combinar los datos
        setProduct({
          ...productData,
          fragances: fraganceData,
        });
      } catch (err) {
        setError("Hubo un error al cargar el producto.",err);
      } finally {
        setLoading(false);
      }
    };

    fetchProduct();
  }, [id]);

  if (loading) {
    return <div className="text-center mt-10">Cargando producto...</div>;
  }

  if (error) {
    return <div className="text-center mt-10 text-red-500">{error}</div>;
  }

  return (
    <div className="container mx-auto px-4 py-8 flex flex-wrap lg:flex-nowrap">
      {/* Imagen del Producto */}
      <div className="w-full lg:w-1/2 mb-6 lg:mb-0">
        <Image
          src={product.image_url}
          alt={product.name}
          className="rounded-lg shadow-md w-full h-auto object-cover"
        />
      </div>

      {/* Detalles del Producto */}
      <div className="w-full lg:w-1/2 p-6">
        <h1 className="text-3xl font-semibold mb-4">{product.name}</h1>
        <p className="text-gray-700 mb-4">
          <strong>Descripción:</strong> {product.description}
        </p>
        <p className="text-gray-700 mb-4">
          <strong>Precio:</strong> ${product.price}
        </p>
        <p className="text-gray-700 mb-4">
          <strong>Intensidad:</strong> {product.intensity} / 5
        </p>

        {/* Fragancias Relacionadas */}
        <div className="mt-4">
          <h3 className="text-lg font-semibold mb-2">Fragancias:</h3>
          {product.fragances?.length > 0 ? (
            <ul className="space-y-4">
              {product.fragances.map((fragance, index) => (
                <li
                  key={index}
                  className="p-4 border rounded-md shadow-sm bg-gray-50"
                >
                  <p className="mb-2">
                    <strong>Nombre:</strong> {fragance.name}
                  </p>
                  <p className="mb-2 flex items-center">
                    <strong>Color:</strong>{" "}
                    <span
                      style={{ backgroundColor: fragance.color }}
                      className="inline-block w-6 h-6 rounded-full border ml-2"
                    ></span>
                  </p>
                  <p>
                    <strong>Descripción:</strong> {fragance.description}
                  </p>
                </li>
              ))}
            </ul>
          ) : (
            <p>No hay fragancias asociadas a este producto.</p>
          )}
        </div>
      </div>
    </div>
  );
};

export default ProductDetail;
